/**
 * 获取渠道的下拉框数据
 * @const
 * @type {String}
 * @default  '[CP_PATH]/internetChannelController/getFunctionChannel'
 */
export const GET_FUNCTION_CHANNEL = CP_PATH + '/internetChannelController/getFunctionChannel'

/**
 * 获取渠道的下拉框数据
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getFunctionChannel(data) {
  return ajax({
    method: 'post',
    headers: {
      "Content-Type": "application/json;charset=utf8"
    },
    data: data,
    url: GET_FUNCTION_CHANNEL
  })
}

