<template>
    <div class="msgStrategyAssess">
        <div class="header">
            <div class="selectNav">
                <el-input
                    class="input_300"
                    placeholder="标签名称"
                    size="small"
                    v-model="tagName"
                    clearable>
                </el-input>
                <el-button type="primary" size="small" icon="el-icon-search" @click="GetQdRcList">查询
                </el-button>
            </div>
        </div>
        <div class="content">
            <div class="title">推荐客户配置</div>
            <div class="tableTd">
                <el-table
                    v-loading="loading"
                    border
                    :data="tableData"
                    style="width: 100%"
                    :row-class-name="tableRowClassName">
                    <el-table-column
                        label="序号"
                        align="center"
                        type="index"
                        width="70">
                    </el-table-column>
                    <el-table-column
                        prop="tagName"
                        label="标签名称">
                    </el-table-column>
                    <el-table-column
                        prop="sendNum"
                        label="短信短信发送客户数">
                        <template slot-scope="scope">
                            <el-link type="primary"  @click="handleDetails(scope.row,'1')">{{scope.row.sendNum}}</el-link>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="successNum"
                        label="成功发送客户数">
                        <template slot-scope="scope">
                            <el-link type="primary"  @click="handleDetails(scope.row,'2')">{{scope.row.successNum}}</el-link>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="callNum"
                        label="发送短信后来电客户数">
                        <template slot-scope="scope">
                            <el-link type="primary"  @click="handleDetails(scope.row,'3')">{{scope.row.callNum}}</el-link>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="sendTime"
                        label="短信发送时间">
                    </el-table-column>
                </el-table>
                <div class="block">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pages.pageNo"
                        :page-sizes="[8, 10, 20, 50]"
                        :page-size="pages.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="pages.total">
                    </el-pagination>
                </div>
            </div>
        </div>
        <!--        //客户数详情-->
        <el-dialog
            class="successList-dialog"
            :visible.sync="khNoShow"
            width="90%"
            top="5vh"
            @closed="closedDialog"
            :close-on-click-modal="false"
            :close-on-press-escape="false">
              <span slot="title" class="dialog-header">
                <span class="header-text">{{ '客户详情' }}</span>
              </span>
            <div class="dialog-content" v-loading="loadingDetails">
                <div class="selectNav">
                    <el-input
                        class="input_300"
                        placeholder="客户名称"
                        size="small"
                        v-model="yhmcDet"
                        clearable>
                    </el-input>
                    <el-input
                        class="input_300"
                        placeholder="客户编号"
                        size="small"
                        v-model="yhbhDet"
                        clearable>
                    </el-input>
                    <el-button type="primary" size="small" icon="el-icon-search" @click="handleDetails(detRow,10)">查询 </el-button>
                    <el-button type="primary" size="small" icon="el-icon-search" @click="exportExc">导出excel
                    </el-button>
                </div>
                <div style="height: 500px">
                    <bdaTable
                        :tableData="successListData"
                        :isPage="true"
                        :height="500"
                        :pageConfig="successListPageConfig"
                        @changePage="changePage"
                    ></bdaTable>
                </div>

            </div>
        </el-dialog>
    </div>
</template>

<script>
  import Interface from '@/base/mixin/channel-preference';
  import Interface2 from '@/base/mixin/fee-collection';
  import bdaTable from '@/components/bda-table/bda-table';
  import {CP_PATH} from "@/config"
  // import {}
  export default {
    name: "msgStrategyAssess",
    mixins: [Interface,Interface2],
    components: {bdaTable},
    props: {},
    data() {
      return {
        //导航模块变量
        tagName: '',
        //首页表格
        pages: {
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        tableData: [],
        loading: false,
        homeTableId:'',
        //详情弹窗
        successListVisible: false,
        successListData: {},
        successListQueryForm: {
          current: 1,
          size: 10,
        },
        successListPageConfig: {
          name: "successList",
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40, 50],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        },
        khNoShow:false,
        loadingDetails:false,
        yhbhDet:'',
        yhmcDet:'',
        detRow:{},
        openType:'1'
      }
    },
    mounted() {
      this.GetQdRcList()
    },
    methods: {
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.GetQdRcList()
        window.console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.GetQdRcList()
        window.console.log(`当前页: ${val}`);
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex % 2 === 0) {
          return 'success-row';
        }
        return '';
      },

      //获取推荐
      GetQdRcList() {
        this.loading = true;
        this.getSearch({
          "tagName": this.tagName,
          "size": this.pages.pageSize,
          "current": this.pages.pageNo,
          "isMessage": true,
        }).then(res => {
          this.loading = false;
          this.pages.total = res.total
          this.tableData = res.records
        })
      },

      /**
       * //客户详情
       * **/
      //改变分页
      changePage(data) {
        if (data.name === 'successList') {
          // alert(data.current)
          this.successListQueryForm.size = data.size
          this.successListQueryForm.current = data.current
          this.handleDetails(this.detRow)
        }
      },
      exportExc(){
        window.location.href = CP_PATH+'/customerConfig/messageExport?id='+this.detRow.id+'&yhmc='+this.yhmcDet+'&yhbh='+this.yhbhDet+'&type='+this.openType
      },
      closedDialog(){
        this.yhmcDet = ''
        this.yhbhDet = ''
        this.successListQueryForm.current = 1
      },
      //详情弹窗
      handleDetails(row,tp=0){
        if (tp!=0){
          this.openType = tp
        }
        if (tp===10){ //查询后回到第一页
          this.successListQueryForm.current = 1
        }
        this.detRow = row
        this.khNoShow = true
        this.loadingDetails = true
        let columnNameData = [
          { prop: 'yhbh', label: '客户编号' },
          { prop: 'yhmc', label: '客户名称' },
          { prop: 'yddz', label: '用电地址' },
          { prop: 'yhlb', label: '用户类型' },
          { prop: 'ydlx', label: '用电类别' ,},
          { prop: 'lxdh', label: '短信发送号码' },
          { prop: 'sendtime', label: '短信发送时间',width: 120 },
          { prop: 'dq', label: '地区' },
          { prop: 'gddw', label: '供电所' },
          { prop: 'calltime', label: '来电时间' },
        ]
        this.messageByCustomers({
          "id":this.detRow.id,
          "yhmc":this.yhmcDet,
          "yhbh":this.yhbhDet,
          "type":this.openType,
          "current": this.successListQueryForm.current,
          "size": this.successListQueryForm.size,
        }).then(res => {
          this.loadingDetails = false
          this.successListPageConfig.total = res.total
          this.successListPageConfig.current = this.successListQueryForm.current
          this.successListPageConfig.size = this.successListQueryForm.size
          this.successListData = {
            tableData: [
              ...res.records
            ],
            headerColor: '#1899fa',
            title: "详情",
            name: 'successList',
            size: this.successListQueryForm.size,
            current: this.successListQueryForm.current,
            total: this.successListPageConfig.total,
            columnNameData
          }
        })
      },
    }
  }
</script>

<style scoped lang="scss">
    .msgStrategyAssess {
        .input_td {
            width: 12%;
            margin-right: 20px;
        }

        .input_300 {
            width: 12%;
            margin-right: 20px;
        }
        ::v-deep .el-table .warning-row {
            // background: #e7f6f7;
            background: #e9f3fe;
        }

        ::v-deep .el-table {
            .success-row {
                background: rgb(244, 244, 244);
            }

            .cell {
                padding: 2px 4px;
            }

            th {
                background-color: rgb(24, 153, 250);

                .cell {
                    color: #ffffff;
                }
            }
        }

        .header {
            line-height: 30px;

            .selectNav {
                text-align: center;
                height: 30px;
                margin-bottom: 16px;
            }

        }

        .content {
            .title {
                padding-left: 18px;
                border-left: 6px solid #1A84F4;
                font-weight: bold;
                margin-bottom: 10px;
            }

            .tableTd {
                .block {
                    text-align: center;
                    margin-top: 10px
                }

            }
        }
        ::v-deep .dialog-header {
            .header-text {
                padding-left: 18px;
                border-left: 6px solid #1A84F4;
                font-weight: bold;
                margin-bottom: 10px;
            }
        }
        ::v-deep .dialog-content{
            padding: 8px 12px;
            min-height: 600px;
            .addLeft{
                width: 300px;
                margin-right: 20px;
                display: inline-block;
                vertical-align: top;
            }
            .addRight{
                width: calc(100% - 320px);
                display: inline-block;
                vertical-align: top;
            }
        }
    }
</style>